.pictures {
  padding-top: 30px;
  padding-bottom: 45px;
  background-color: $basic-white;

  @media #{$tablet-min} {
    padding-top: 75px;
    padding-bottom: 65px;
  }

  @media #{$desktop-min} {
    padding-bottom: 60px;
  }
}

.pictures__intro {
  @include c;
  width: 250px;
  margin-bottom: 50px;
  line-height: 2.14;

  @media #{$tablet-min} {
    width: 550px;
    margin-bottom: 75px;
    font-size: 18px;
    line-height: 1.66;
  }

  @media #{$desktop-min} {
    width: 640px;
    margin-bottom: 92px;
  }
}

.pictures__wrapper {
  @include c280;
  display: grid;
  margin-top: 20px;
  gap: 20px;
  grid-template-columns: 1fr;

  @media #{$tablet-min} {
    @include c620;
    margin-top: 50px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: minmax(455px, 1fr);
  }

  @media #{$desktop-min} {
    @include c940;
    grid-row-gap: 50px;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.pictures__image {
  img {
    display: block;
    width: 280px;
    height: 180px;

    @media #{$tablet-min} {
      width: 300px;
      height: 300px;
    }

    @media #{$desktop-min} {
      width: 300px;
      height: 400px;
    }
  }

  .pictures__item--day-choice & {
    position: relative;

    img {
      @include c;
      width: 320px;
      height: 137px;

      @media #{$tablet-min} {
        width: 660px;
        height: 509px;
      }

      @media #{$desktop-min} {
        width: 1200px;
        height: 506px;
      }
    }

    &::before {
      position: absolute;
      z-index: 1;
      width: 140px;
      padding-top: 8px;
      padding-bottom: 8px;
      background-color: $basic-pink;
      color: $basic-white;
      content: "Панорама дня";
      font-size: 12px;
      font-weight: 700;
      text-decoration: none;
      text-transform: uppercase;

      @media #{$tablet-min} {
        top: 60px;
        left: 170px;
      }

      @media #{$desktop-min} {
        top: 50px;
        left: 330px;
      }
    }
  }
}

.pictures__text-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 12px;

  @media #{$tablet-min} {
    margin-top: 5px;
  }
}

.pictures__text {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: space-between;
  padding: 18px 5px 20px 17px;
  background-color: $grey-95;

  @media #{$tablet-min} {
    min-height: 155px;
    padding: 22px 30px 26px 27px;
  }

  .pictures__item--day-choice & {
    @include c280;

    @media #{$tablet-min} {
      @include c620;
      min-height: min-content;
      flex-direction: row;
      flex-wrap: wrap;
      padding-bottom: 15px;
    }

    @media #{$desktop-min} {
      @include c940;
      padding-bottom: 10px;
    }
  }
}

.pictures__username {
  margin: 0;
  font-weight: bold;
  text-align: left;
}

.pictures__time {
  font-size: 12px;
  font-weight: 300;
  text-align: left;
  text-transform: uppercase;
}

.pictures__comment {
  margin: 0;
  font-weight: 300;
  letter-spacing: 0.3px;
  line-height: 1.8;
  text-align: left;

  br {
    @media #{$tablet-min} {
      display: none;
    }
  }

  .pictures__item--day-choice & {
    margin-bottom: 10px;

    @media #{$tablet-min} {
      order: 1;
    }
  }
}

.pictures__likes {
  color: $basic-pink;
  font-size: 12px;
  text-align: left;
  text-transform: uppercase;

  @media #{$tablet-min} {
    display: flex;
    justify-content: space-between;
  }

  .pictures__item--day-choice & {
    @media #{$tablet-min} {
      align-self: flex-end;
    }
  }
}

.pictures__likes-icon {
  margin-right: 15px;
  fill: currentColor;

  @media #{$tablet-min} {
    align-self: center;
    order: 1;
    margin-right: 0;
    margin-left: 17px;
  }
}
